___CSS_LOADER_EXPORT___.push([module.id, `.todo-container {
    display: grid;
    grid-template-columns: min-content max-content;
    grid-template-rows: auto;
    padding: 20px;
    padding-left: 0px;
    margin: 10px 0px 10px 20px;
    row-gap: 10px;
    column-gap: 5px;
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 5px;
}

.expand-todo,.remove-todo {
    grid-column-start: 2;
}

.todo-title {
    font-weight: bold;
    font-size: large;
}

.todo-date {
    grid-column-start: 2;
    color: blueviolet;
    font-weight: bold;
    font-size: smaller;
}

.todo-button {
    font-family: Arial, Helvetica, sans-serif;
    background-color: #60a5fa;
    border: solid 2px #60a5fa;
    color: white;
    border-radius: 15px;
    font-weight: bold;
    padding: 5px;
    width: 80px;
}

.task-form,.edit-task-form,.project-form {
    display: flex;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
    font-size: large;
    font-weight: bold;
    gap: 10px;
}

.submit,.save-changes,.submit-project {
    width: max-content;
    padding: 5px 10px;
    background-color: #60a5fa;
    color: white;
    font-weight: bold;
    border-radius: 5px;
    align-self: center;
}

.add-task-dialog,.edit-task-dialog,.add-project-dialog {
    width: 20vw;
    border: none;
    border-radius: 5px;
}

dialog::backdrop {
    backdrop-filter: blur(1px);
}

.edit-task-dialog {
    margin-top: 35vh;
    border: 1px solid gray;
}`, "",{"version":3,"sources":["webpack://./src/todo.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,8CAA8C;IAC9C,wBAAwB;IACxB,aAAa;IACb,iBAAiB;IACjB,0BAA0B;IAC1B,aAAa;IACb,eAAe;IACf,yCAAyC;IACzC,kBAAkB;AACtB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,oBAAoB;IACpB,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,yCAAyC;IACzC,yBAAyB;IACzB,yBAAyB;IACzB,YAAY;IACZ,mBAAmB;IACnB,iBAAiB;IACjB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,yCAAyC;IACzC,gBAAgB;IAChB,iBAAiB;IACjB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,yBAAyB;IACzB,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,gBAAgB;IAChB,sBAAsB;AAC1B","sourcesContent":[".todo-container {\n    display: grid;\n    grid-template-columns: min-content max-content;\n    grid-template-rows: auto;\n    padding: 20px;\n    padding-left: 0px;\n    margin: 10px 0px 10px 20px;\n    row-gap: 10px;\n    column-gap: 5px;\n    font-family: Arial, Helvetica, sans-serif;\n    border-radius: 5px;\n}\n\n.expand-todo,.remove-todo {\n    grid-column-start: 2;\n}\n\n.todo-title {\n    font-weight: bold;\n    font-size: large;\n}\n\n.todo-date {\n    grid-column-start: 2;\n    color: blueviolet;\n    font-weight: bold;\n    font-size: smaller;\n}\n\n.todo-button {\n    font-family: Arial, Helvetica, sans-serif;\n    background-color: #60a5fa;\n    border: solid 2px #60a5fa;\n    color: white;\n    border-radius: 15px;\n    font-weight: bold;\n    padding: 5px;\n    width: 80px;\n}\n\n.task-form,.edit-task-form,.project-form {\n    display: flex;\n    flex-direction: column;\n    font-family: Arial, Helvetica, sans-serif;\n    font-size: large;\n    font-weight: bold;\n    gap: 10px;\n}\n\n.submit,.save-changes,.submit-project {\n    width: max-content;\n    padding: 5px 10px;\n    background-color: #60a5fa;\n    color: white;\n    font-weight: bold;\n    border-radius: 5px;\n    align-self: center;\n}\n\n.add-task-dialog,.