    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    font-size: 25px;
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 55px;
    color: #737373;
}

aside {
    background-color: #fcfaf6;
    display: grid;
    grid-template-columns: max-content 1fr;
    grid-template-rows: repeat(3,min-content);
    column-gap: 15px;
    padding: 20px;
}
.sidebar-button {
    grid-column-start: 1;
    padding: 10px;
    color: white;
    background-color: #f87171;
    font-weight: bold;
    border-radius: 5px;
}


.add-project {
    grid-column-start: 2;
}

.project {
    font-size: large;
    border-radius: 5px;
    padding: 8px 0px 8px 8px;
    color: black;
    
}

.project:hover {
    color: white;
    background-color: #60a5fa
}
